

































































































import uniPopup from "../../../components/uni-popup/uni-popup.vue"

export default {
	mounted() {

		// 自提点
		uni.request({
			url: this.config.url + "member/station",
			method: "post",
			data: {
				token: this.token
			},
			success: (res) => {
				console.log(res)
				this.selfList = res.data.data;
			}

		})
		//我的收货地址
		uni.request({
			url: this.config.url + "member/address",
			method: "POST",
			data: {
				token: this.token
			},
			success: (res) => {
				console.log(res)
				this.homeList = res.data.data;

			}
		})
	},
	components: {
		uniPopup
	},
	data() {
		return {
			src1: '../../../static/img/address/y-duihao.png',
			src2: '../../../static/img/address/n-duihao.png',
			typeClass: 'home',
			subState: 1,
			isSelect: false,
			homeList: [],
			selfList: [],
			
		};
	},
	onShow() {

	},
	onLoad(e) {
		if (e.type == 'select') {
			this.isSelect = true;
		}
	},
	methods: {
		// 删除我的收货地址
		removeH(index, id) {
			console.log(index, id)
			uni.request({
				url: this.config.url + "address/del",
				data: {
					token: this.token,
					address_id: id,
				},
				method: "post",
				success: (res) => {
					console.log(res)
				}
			});
		},
		// 删除自提点
		removeS(index, id) {

			uni.request({
				url: this.config.url + "station/del",
				data: {
					token: this.token,
					address_id: id,
				},
				method: "post",
				success: (res) => {
					console.log(res)
				}
			});
		},

		// uni.showToast({
		// 	icon: "success",
		// 	title: '操作成功!',
		// 	duration: 2000
		// });


		switchType(type) {

			this.typeClass = type;
			if (type == "home") {
				this.subState = 1;

			} else if (type == "self") {
				this.subState = 2;
			}
		},
		edit(row) {
			var type = JSON.stringify(row)

			uni.navigateTo({
				url: "/pages/user/address/edit/edit?type=" + type
			});

		},
		clear(row) {
			console.log(row)
		},
		add() {
			uni.navigateTo({
				url: "edit/edit"
			})
		},
		select(row) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			uni.setStorage({
				key: 'selectAddress',
				data: row,
				success() {
					uni.navigateBack();
				}
			})
		}
	}
}
